import React from 'react'
import EditRow from './EditRow'
import TableRow from './TableRow'

export class TablePanel extends React.Component {
  constructor(props) {
    super(props)
    this.refreshView = this.refreshView.bind(this)
    this.displayEditionForm = this.displayEditionForm.bind(this)
    this.state = {
      data: [],
      currentPage: 1,
      handlers: {
        displayEditionForm: this.displayEditionForm
      }
    }
  }

  getData(page) {
    var self = this;
    var req = {
      method: 'GET',
      mode: 'cors',
      cache: 'default'
    }
    fetch('api/limit' + this.props.definition.apiUrl + '/' + page, req)
      .then(function (res) {
        return res.json()
      })
      .then(function (json) {
        var data = json.map(function (obj) {
          obj.editMode = false
          return obj
        })
        this.setState({
          data: data
        })
      }.bind(this))
      .catch(function (err) {
        console.error(err);
      })
  }

  refreshView() {
    this.getData(this.state.currentPage)
  }

  displayEditionForm (rowId) {
    let dataCopy = this.state.data
    for (obj of dataCopy) {
      if (obj.id === rowId) {
        obj.editMode = true
        break
      }
    }
    this.setState({data: dataCopy})
  }

  dismissEditingForm() {

  }

  displayCreationForm() {

  }

  initializeButtons(level) {

  }

  componentDidMount() {
    this.refreshView()
  }

  render() {
    var self = this;
    var tableHeader = self.props.definition.fields.map(function (col) {
      return (col.name === 'id' || col.name === 'valid') ? null : <th key={self.props.definition.alias + 'def' + col.name}>{col.label}</th>
    })
    var tableBody = this.state.data.map(function (obj) {
      if(obj.editMode) {
        return <EditRow hideForm={this.dismissEditingForm} objectId={this.props.definition.id} apiUrl={this.props.definition.apiUrl} key={this.props.definition.id + '_cf'} definition={self.props.definition.fields} data={obj} updateMessage={this.props.handlers.updateMessage} refreshView={this.refreshView} />
      }
      else {
        return <TableRow key={self.props.definition.name + obj.id} identifier={self.props.definition.name + obj.id} data={obj} activable={self.props.definition.activable} apiUrl={self.props.definition.apiUrl} handlers={self.props.handlers}/>
      }
    })
    return (
      <div className="table">
        <table className="table table-bordered">
          <thead>
            <tr>
              {tableHeader}
              <th>Actions</th>
            </tr>
          </thead>
          <tbody>
            {this.props.displayForm ? <EditRow hideForm={this.props.handlers.hideCreationForm} objectId={this.props.definition.id} apiUrl={this.props.definition.apiUrl} key={this.props.definition.id + '_cf'} definition={this.props.definition.fields} updateMessage={this.props.handlers.updateMessage} refreshView={this.refreshView} /> : null}
            {tableBody}
          </tbody>
        </table>
      </div>
    )
  }
}

export default TablePanel



