import React from 'react'
import ActionButton from './ActionButtons'

export class TableRow extends React.Component {
  constructor(props) {
    super(props)
    this.deleteRow = this.deleteRow.bind(this)
    this.activateRow = this.activateRow.bind(this)
    var valid = this.props.activable && this.props.data.valid === '1';
    this.state =  {
      displayRow: true,
      modifyRow: false,
      isRowValid: valid,
      handlers: {
        delete: this.deleteRow,
        modify: this.displayEditionForm,
        activate: this.activateRow,
        updateMessage: this.props.handlers.updateMessage
      }
    }
  }

  activateRow() {
    this.setState({isRowValid: !this.state.isRowValid})
  }

  deleteRow() {
    this.setState({displayRow: false});
  }

  render() {
    var self = this;
    var handlers = {
      delete: this.deleteRow
    }
    var fieldContent = Object.keys(self.props.data).map(function (field) {
      return (
        (field === 'id' || field === 'valid' || field === 'editMode') ? null : <td key={self.props.identifier + field}>{self.props.data[field]}</td>
      )
    })
    if (!this.state.displayRow) {
      //delete
      return null
    }
    else if (!this.state.modifyRow) {
      //init state
      return (
        <tr className={this.state.isRowValid ? 'active' : ''}>
          {fieldContent}
          <ActionButton alias={this.props.data.alias} id={this.props.data.id} activable={this.props.activable} valid={this.state.isRowValid} apiUrl={this.props.apiUrl} handlers={this.state.handlers}/>
        </tr>
      )
    }
    else {
      //modify
      return null
    }
  }
}

export default TableRow



