import React from 'react'
import NavbarResp from './NavbarResp'
import Sidebar from './Sidebar'
import View from './View'

export class Wrapper extends React.Component {
  constructor(props) {
    super(props)
    this.updateView = this.updateView.bind(this)
    this.state = {
      activeObject: 0,
      menuData: [],
      objectIds: [],
      viewObjects: [],
      handlers: {
        updateView: this.updateView
      }
    }
  }

  getViewObjects() {
    var req = {
      method: 'GET',
      mode: 'cors',
      cache: 'default'
    }
    fetch('/init', req)
      .then(function (res) {
        return res.json();
      })
      .then(function (data) {
        if(data.msg === 'OK') {
          var activeObject = null
          data.obj.forEach(function (object) {
            if (object.default === true) {
              activeObject = object.id
            }
          })
          this.setState({
            activeObject: activeObject,
            viewObjects: data.obj
          })
        }
        else {
          console.err(data.obj)
        }
      }.bind(this))
  }

  updateView(id, refresh = false) {
    if (refresh) {
      //fetch new definition
    }
    else {
      this.setState({
        activeObject: id
      })
    }
  }

  componentDidMount() {
    this.getViewObjects()
  }

  render() {
    var self = this
    var views = this.state.viewObjects.map(function (object) {
      if (object.id === self.state.activeObject) {
        return (
          <View key={object.viewId} definition={object}/>
        )
      }
    })
    return (
      <div id="page">
        <nav className="navbar navbar-default navbar-static-top" role="navigation">
          <NavbarResp />
          <Sidebar activeObject={self.state.activeObject} data={self.state.viewObjects} handlers={this.state.handlers} />
        </nav>
        <div id="page-wrapper" className="container-fluid">
          <div id="main" className="row">
            {views}
          </div>
        </div>
      </div>
    )
  }
}

export default Wrapper



