import React from 'react'
import MenuEntry from './MenuEntry'
import MenuGroup from './MenuGroup'

export class Sidebar extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      groups: [],
      items: []
    }
  }

  getUsedGroups() {
    var req = {
      method: 'GET',
      mode: 'cors',
      cache: 'default'
    }
    fetch('/api/maintenance/usedgroups', req)
      .then(function (res) {
        return res.json()
      })
      .then(function (data) {
        var groups = []
        for (const i in data) {
          var gpData = data[i]
          var gp = {
            definition: gpData,
            items: []
          }
          groups[gpData.id] = gp
        }
        this.setState({groups: groups})
      }.bind(this))
  }

  componentWillMount() {
    this.getUsedGroups()
  }

  render() {
    var self = this
    var groups = this.state.groups
    var menuEntries = this.props.data.map(function (object) {
      if (object.groupId === '') {
        return (
          <MenuEntry objectId={object.id} active={object.id === self.props.activeObject ? 'active' : ''} key={object.id + '_link'} label={object.label} updateView={self.props.handlers.updateView} insideGroup={false}/>
        )
      }
      else {
        groups[object.groupId].items.push(object)
      }
    })
    var groupEntries = groups.map(function (group) {
      return (
        <MenuGroup group={group} activeObject={self.props.activeObject} key={group.definition.id + '_grouplink'} updateView={self.props.handlers.updateView} />
      )
    })
    return (
      <div className="navbar-default sidebar" role="navigation" id="sidebar">
        <div className="sidebar-nav navbar-collapse">
          <ul className="nav">
            {menuEntries}
            {groupEntries}
          </ul>
        </div>
      </div>
    )
  }
}

export default Sidebar



